import React, { Component } from 'react'
import './App.css'
import Main from './Main'
import Envelope from './components/Envelope'
class App extends Component {
  state = {
    class: 'bounceInLeft',
    show: false
  }
  toggle = () => {
    this.setState({ show: !this.state.show })
  }
  render() {
    const main = () => {
      if (this.state.show) return <Main />
    }
    return (
      <div>
        <Envelope toggle={this.toggle} />
        {main()}
      </div>
    )
  }
}

export default App



