import React, { Component } from 'react'
import countDownText1 from '../icon/countDownText1.png'
import countDownText2 from '../icon/countDownText2.png'
import dayText from '../icon/day.png'
import hourText from '../icon/hour.png'
import minuteText from '../icon/minute.png'
import secondText from '../icon/second.png'
import openText from '../icon/openText.png'
import heartBreak from '../icon/heart-break.png'

// 成全
const chengquan =
  'http://other.web.ri01.sycdn.kuwo.cn/resource/n3/21/39/4068308060.mp3'
// 五月雨
const wuyueyu =
  'http://other.web.rh01.sycdn.kuwo.cn/resource/n1/77/22/2544447920.mp3'

const audioSrc = Math.random() > 0.5 ? chengquan : wuyueyu

class Envelope extends Component {
  state = {
    class: 'bounceInLeft',
    canOpened: false,
    leftDate: {},
    showAlert: false
  }
  componentDidMount() {
    const audio = this.refs.envelopeAudio
    console.log(audio.played, audio.error)

    this.check(2018, 1, 13)
    setInterval(() => {
      this.check(2018, 1, 13)
      if (audio.played.length === 0) {
        audio.play()
      }
      console.log(audio.played, audio.error)
    }, 1000)
  }

  handleClick = () => {
    const { toggle } = this.props
    if (this.state.canOpened) {
      const audio = this.refs.envelopeAudio
      audio.pause()
      audio.currentTime = 0
      toggle()
      this.setState({ class: 'bounceOutRight' })
    } else {
      alert('请不要着急，还没到时间噢！')
    }
  }
  judgeCanOpened(year, month, day) {
    return new Date(year, month - 1, day) <= new Date()
  }
  computeLeftDate = (year, month, day) => {
    var dateNow = new Date()
    var dateJNR = new Date(year, month - 1, day)
    // var anniversary = parseInt((dateNow - dateJNR) / (365*24*3600*1000))
    var d = parseInt((dateJNR - dateNow) / (24 * 3600 * 1000))
    var hour = parseInt(((dateJNR - dateNow) / (3600 * 1000)) % 24)
    var minute = parseInt(((dateJNR - dateNow) / (1000 * 60)) % 60)
    var second = parseInt(((dateJNR - dateNow) / 1000) % 60)
    return { d: d, hour: hour, minute: minute, second: second }
  }
  check(year, month, day) {
    const leftDate = this.computeLeftDate(year, month, day)
    const canOpened = this.judgeCanOpened(year, month, day)
    this.setState({
      leftDate,
      canOpened
    })
  }
  render() {
    const { canOpened } = this.state
    const { d, hour, minute, second } = this.state.leftDate

    return (
      <div className={'envelope animated ' + this.state.class}>
        <audio ref="envelopeAudio" src={audioSrc} />
        <div className="text">
          {!canOpened ? (
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <img src={countDownText1} />
              <span
                style={{
                  color: '#ff6347',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center'
                }}
              >
                {d}
                <img src={dayText} />
                {hour}
                <img src={hourText} />
                {minute}
                <img src={minuteText} />
                {second}
                <img src={secondText} />
              </span>
              <img src={countDownText2} />
            </div>
          ) : (
            <img src={openText} />
          )}
        </div>
        <div className="triangle-down" />
        <canvas className="fireworks" width="100%" height="100%" />
        <img
          id="heart"
          onClick={this.handleClick}
          className="heart"
          src={heartBreak}
          style={{ fontSize: '40px' }}
        />
        <div className="triangle-up" />
      </div>
    )
  }
}

export default Envelope



